<template>
  <div id="app">
    <div v-if="selected" style="padding-top:10px; width: 100%;">
      You have selected <code>{{selected.name}}, navigating to: {{selected.url}}</code>
    </div>
    <div class="autosuggest-container">
      <vue-autosuggest
              v-model="query"
              :suggestions="filteredOptions"
              @focus="focusMe"
              @click="clickHandler"
              @input="onInputChange"
              @selected="onSelected"
              :renderSuggestion="renderSuggestion"
              :sectionConfigs="sectionConfigs"
              :get-suggestion-value="getSuggestionValue"
              :input-props="{id:'autosuggest__input', placeholder:'Search for anything!'}"
              v-focus>
        <div slot-scope="{suggestion}" style="display: flex; align-items: center;">
          <img :style="{ display: 'flex', width: '25px', height: '25px', borderRadius: '15px', marginRight: '10px'}" :src="suggestion.item.icon" />
          <div style="{ display: 'flex', color: 'navyblue'}">{{suggestion.item.name}}</div>
        </div>
      </vue-autosuggest>
    </div>
  </div>
</template>

<script>
import { VueAutosuggest } from "vue-autosuggest";

export default {
  data() {
    return {
      query: "",
      selected: "",
      sectionConfigs: {
        'default': {
          limit: 7
        }
      },
      suggestions: [
        {
          data: [
            {
              id: 1,
              type: "navigation",
              name: "Transactions",
              label: "See all transactions",
              url: "/transactions",
              icon: "https://static.paddle.com/assets/images/icons/navigation/transactions.png"
            },
            {
              id: 2,
              type: "navigation",
              name: "Subscribers",
              label: "See all subscribers",
              url: "/subscribers",
              icon: "https://static.paddle.com/assets/images/icons/navigation/customers.png"
            },
            {
              id: 3,
              type: "navigation",
              name: "Overview",
              url: "/overview",
              icon: "https://static.paddle.com/assets/images/icons/navigation/dashboard.png"
            },
            {
              id: 4,
              type: "navigation",
              name: "Plans",
              url: "/plans",
              icon: "https://static.paddle.com/assets/images/icons/navigation/catalog.png"
            },
            {
              id: 5,
              type: "navigation",
              name: "Products",
              url: "/products",
              icon: "https://static.paddle.com/assets/images/icons/navigation/catalog.png"
            },
            {
              id: 6,
              type: "navigation",
              name: "Coupons",
              url: "/coupons",
              icon: "https://static.paddle.com/assets/images/icons/navigation/catalog.png"
            },
            {
              id: 7,
              type: "navigation",
              name: "Invoicing",
              url: "/payment-requests",
              icon: "https://static.paddle.com/assets/images/icons/navigation/invoicing.png"
            },
            {
              id: 8,
              type: "navigation",
              name: "Reports",
              url: "/reports",
              icon: "https://static.staging.paddle-internal.com/assets/images/icons/navigation/reports.png"
            },
            {
              id: 9,
              type: "navigation",
              name: "Payouts",
              url: "/payouts/complete",
              icon: "https://static.staging.paddle-internal.com/assets/images/icons/navigation/account.png"
            },
            {
              id: 10,
              type: "faq",
              name: "How do we calculate your MRR?\n",
              url: "https://paddle.com/support/how-we-calculate-your-mrr/"
            },
            {
              id: 11,
              type: "definition",
              name: "MRR",
              definition: "Monthly recurring revenue (MRR) is income that a business can count on receiving every single month.\n"
            }

          ]
        }
      ]
    };
  },
  computed: {
    defaultOptions() {
      return [
        {
          id: 1, 
          type: "search", 
          name: "Transactions", 
          label: "Search Transactions for",
          url: "/transactions?q=" + this.query,
          icon: "https://static.paddle.com/assets/images/icons/navigation/transactions.png"
        },
        {
          id: 2, 
          type: "search", 
          name: "Subscribers", 
          label: "Search Subscribers for \"" + this.query + "\"",
          url: "/subscribers?q=" + this.query,
          icon: "https://static.paddle.com/assets/images/icons/navigation/customers.png"
        },
        {
          id: 3,
          type: "search",
          name: "Plans",
          label: "Search Subscription Plans for \"" + this.query + "\"",
          url: "/subscribers?q=" + this.query,
          icon: "https://static.paddle.com/assets/images/icons/navigation/catalog.png"
        },
        {
          id: 4, 
          type: "search", 
          name: "Admin",
          label: "Search Sellers for \"" + this.query + "\"",
          url: "/subscribers?q=" + this.query,
          icon: "https://static.staging.paddle-internal.com/assets/images/icons/navigation/vendor_management.png"
        }
      ]
    },
    filteredOptions() {
      let filteredData = this.suggestions[0].data.filter(option => {
          return option.name.toLowerCase().indexOf(this.query.toLowerCase()) > -1
      });
      if (this.query.length > 0) {
        for (let i = 0; i < this.defaultOptions.length; i++) {
          filteredData.push(this.defaultOptions[i]);
        }
      }

      return [
        {
          data: filteredData
        }
      ];
    }
  },
  methods: {
    clickHandler() {
      // event fired when clicking on the input
    },
    onSelected(item) {
      this.selected = item.item;
    },
    onInputChange(text) {
      // event fired when the input changes
      console.log(text)
    },
    getSuggestionValue(suggestion) {
      return suggestion.item.name;
    },
    focusMe(e) {
      console.log(e) // FocusEvent
    },
    renderSuggestion(suggestion) {
      console.log(suggestion);
      if (suggestion.item.type === "definition") {
        return (
          <div style="display: flex;">
            <div class="category">Definition</div>
            <div class="label">{suggestion.item.definition}</div>
          </div>
        )
      }
      else if (suggestion.item.type === "faq") {
        return (
          <div style="display: flex;">
            <div class="category">FAQ</div>
            <div class="label">{suggestion.item.name}</div>
          </div>
        )
      }
      else if (suggestion.item.type === "search") {
        return (
          <div style="display: flex;">
            <div class="category">
              {suggestion.item.name}
              <span class="results-icon" style={{ background: "center / contain no-repeat url("+suggestion.item.icon+")" }} />
            </div>
          <div class="label">Search {suggestion.item.name} for <b>{this.query}</b></div>
          </div>
        )
      }
      else if (suggestion.item.type === "navigation") {
        return (
          <div style="display: flex;">
            <div class="category">
              {suggestion.item.name}
              <span class="results-icon" style={{ background: "center / contain no-repeat url("+suggestion.item.icon+")" }} />
            </div>
            <div class="label"> Go to <b>{suggestion.item.name}</b></div>
          </div>
        )
      }
      else {
        return (
          <div style="display: flex;">
                <div class="category">
                  {suggestion.item.name}
                  <span class="results-icon" style={{ background: "center / contain no-repeat url("+suggestion.item.icon+")" }} />
                </div>
            <div class="label">{suggestion.item.label}</div>
          </div>
        );
      }
    },
  },
  components: {
    VueAutosuggest
  }
}
</script>

